.popup {
  background-color: rgb(0 0 0 / 50%);
  animation-name: animatetop;
  animation-duration: 0.4s;
  display: none;

  @keyframes animatetop {
    0% {
      top: -300px;
      opacity: 0;
    }

    100% {
      top: 0;
      opacity: 1;
    }
  }

  &__content {
    background-color: var(--white);
    width: 100%;
    box-sizing: border-box;
    padding: 29px 30px 54px 30px;
    border-top: 1px solid var(--gray-dark);
    box-shadow: 0px 5px 10px 0px var(--shadow);
    position: relative;
    top: 50%;
    transform: translateY(-50%);

    @media (min-width: @tablet-width) {
      width: auto;
      margin: 0 101px;
      padding: 38px 104px 46px 108px;
    }

    @media (min-width: @desktop-width) {
      width: 566px;
      margin: 0 auto;
    }
  }

  &__title {
    font-size: 25px;
    line-height: 30px;
    color: var(--black);
    margin: 0;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 14px;

    @media (min-width: @tablet-width) {
      font-size: 30px;
      line-height: 57px;
      margin-bottom: 11px;
    }
  }

  &__button {
    width: 100%;
    margin-top: 31px;
  }
}
