.user-data {
  box-sizing: border-box;

  @media (min-width: @tablet-width) {
    padding: 50px;
  }

  @media (min-width: @desktop-width) {
    padding: 50px 165px 50px 62px;
  }

  &__line {
    @media (min-width: @tablet-width) {
      margin-top: 15px;
    }
  }

  &__block {
    display: flex;
    flex-wrap: wrap;
    gap: 35px;

    @media (min-width: @tablet-width) {
      display: flex;
      flex-direction: column;
      gap: 25px;
    }

    @media (min-width: 1000px) {
      flex-direction: row;
      gap: 55px;
    }

    @media (min-width: @desktop-width) {
      flex-direction: column;
      row-gap: 24px;
    }
  }

  &__label {
    flex-grow: 1;

    @media (min-width: @tablet-width) {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }

  &__input {
    width: 100%;
    position: relative;

    &:hover {
      svg {
        fill: var(--green);
        opacity: 1;
      }
    }

    &:active {
      svg {
        fill: var(--green-hover);
        opacity: 1;
      }
    }

    @media (min-width: @tablet-width) {
      width: 75%;
    }

    @media (min-width: @desktop-width) {
      width: 700px;
    }
  }

  &__phone {
    display: block;
    position: absolute;
    width: 25px;
    height: 24px;
    top: 50%;
    transform: translateY(-50%);
    right: 7px;
    opacity: 0.2;

    @media (min-width: @tablet-width) {
      right: 26px;
    }

    @media (min-width: @desktop-width) {
      right: 8px;
    }
  }

  &__email {
    display: block;
    position: absolute;
    width: 32px;
    height: 21px;
    top: 50%;
    transform: translateY(-50%);
    right: 3px;
    opacity: 0.2;

    @media (min-width: @tablet-width) {
      right: 22px;
    }

    @media (min-width: @desktop-width) {
      right: 4px;
    }
  }
}
